'use strict';
/**
 * @api SET_BACK() SET_BACK
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {boolean} hide=true 是否隐藏，默认隐藏
 * @apiDescription 设置返回按钮
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * SET_BACK()
 * @apiParamExample {string} 返回值
 * 无
 **/
import {WinJSDec} from './APP/config';

var name = 'SET_BACK';
var SET_BACK = (hide = true) => {
  WinJSDec(_ => {
    window.WinJSBridge.call('webview', 'hideBack', {hide});
  });
};
export default SET_BACK;
